:root {
  --lightmode-blue: #98dbc6;
  --lightmode-green: #5bc8ac;
  --lightmode-yellow: #e6d72a;
  --lightmode-pink: #f18d9e;
  --darkmode-slate: #626d71;
  --darkmode-ceramic: #cdcdc0;
  --darkmode-latte: #ddbc95;
  --darkmode-coffee: #b38867;
}
*{
  box-sizing: border-box;
	margin: 0;
	padding: 0;	
	font-family: Raleway, sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--lightmode-blue);
  background: linear-gradient(90deg, var(--lightmode-green), var(--lightmode-pink));	
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

i{
  cursor: pointer;
}
.card {
  background-color: var(--lightmode-yellow);
}

/********************************Navbar*********************************/
.navbar-brand, .aboutHeading{
	color: var(--darkmode-latte);
	font-weight: bolder;
	font-size: 25px;
}
.navbar-brand span,
.aboutHeading span {
	font-size: 30px;
	color: var(--lightmode-yellow);
}

/********************************About*********************************/
.about-section p{
	font-family: Roboto;
	font-weight: 700;
	margin: 50px;
	font-size: large;
}

.img-container{
	border: 2px solid var(--lightmode-yellow);
}
.MainScreenImg{
	width: 80vw;
	height: 80vh;
	object-fit: fill;
}

/********************************Login*********************************/
.login-container {
	display: flex;
	align-items: flex-start;
	justify-content: center;
	min-height: 100%;
	margin-top: 50px;
}
.screen {		
	background: linear-gradient(90deg, var(--lightmode-green), var(--lightmode-pink));		
	position: relative;	
	height: 600px;
	width: 360px;	
	box-shadow: 0px 0px 24px var(--lightmode-blue);
}

.screen__content {
	z-index: 1;
	position: relative;	
	height: 100%;
}

.screen__background {		
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 0;
	-webkit-clip-path: inset(0 0 0 0);
	clip-path: inset(0 0 0 0);	
}

.screen__background__shape {
	transform: rotate(45deg);
	position: absolute;
}

.screen__background__shape1 {
	height: 520px;
	width: 520px;
	background: #FFF;	
	top: -50px;
	right: 120px;	
	border-radius: 0 72px 0 0;
}

.screen__background__shape2 {
	height: 220px;
	width: 220px;
	background: var(--lightmode-blue);	
	top: -172px;
	right: 0;	
	border-radius: 32px;
}

.screen__background__shape3 {
	height: 540px;
	width: 190px;
	background: linear-gradient(270deg, var(--lightmode-green), var(--lightmode-pink));
	top: -24px;
	right: 0;	
	border-radius: 32px;
}

.screen__background__shape4 {
	height: 400px;
	width: 200px;
	background: var(--lightmode-blue);	
	top: 420px;
	right: 50px;	
	border-radius: 60px;
}

.login {
	width: 320px;
	padding: 30px;
	padding-top: 156px;
}

.login__field {
	padding: 20px 0px;	
	position: relative;	
}

.login__icon {
	position: absolute;
	top: 30px;
	color: var(--lightmode-pink);
}

.login__input {
	border: none;
	border-bottom: 2px solid #D1D1D4;
	background: none;
	padding: 10px;
	padding-left: 24px;
	font-weight: 700;
	width: 75%;
	transition: .2s;
}

.login__input:active,
.login__input:focus,
.login__input:hover {
	outline: none;
	border-bottom-color: var(--lightmode-pink);
}

.login__submit {
	background: #fff;
	font-size: 14px;
	margin-top: 30px;
	padding: 16px 20px;
	border-radius: 26px;
	border: 1px solid #D4D3E8;
	text-transform: uppercase;
	font-weight: 700;
	display: flex;
	align-items: center;
	width: 100%;
	color: var(--lightmode-green);
	box-shadow: 0px 2px 2px var(--lightmode-pink);
	cursor: pointer;
	transition: .2s;
}

.login__submit:active,
.login__submit:focus,
.login__submit:hover {
	border-color: var(--lightmode-green);
	outline: none;
}

.button__icon {
	font-size: 24px;
	margin-left: auto;
	color: var(--lightmode-green);
}

.social-login {	
	position: absolute;
	height: 140px;
	width: 160px;
	text-align: center;
	bottom: 0px;
	right: 0px;
	color: #fff;
}

.social-icons {
	display: flex;
	align-items: center;
	justify-content: center;
}

.social-login__icon {
	padding: 20px 10px;
	color: #fff;
	text-decoration: none;	
	text-shadow: 0px 0px 8px #7875B5;
}

.social-login__icon:hover {
	transform: scale(1.5);	
}

.btn {
	background: linear-gradient(90deg, var(--lightmode-yellow), var(--lightmode-blue));	
	border-radius: 5px;
	color: black;
}
/* .navbar-brand {
	color: #f18d9e;
} */

@media screen and (max-width: 800px) {
	
	html, body {
		overflow-x: hidden;
		width: 100%;
	}
	.login-container{
		overflow-y: hidden;
	}

}